import React from 'react';
import './Home.css';
class Home extends React.Component {

  constructor(){
    super();
    this.state = {
      movie: [],
      inputVal: "",
      notfound: false,
      showLoader: "",
      hide: "",
      rating: ""
    }
  }

  componentDidMount(){
    this.nameInput.focus();
  }

  getMovie(e){
    this.setState({
      showLoader: "show",
      // movie: []
    })
    fetch("https://www.omdbapi.com/?t="+this.state.inputVal+"&apikey=BanMePlz")
    .then(res => res.json())
    .then(res => {
      // console.log(res);
        var checkAvail = res.Response === "False" ? true : false;

        this.setState({
          movie: res,
          notfound: checkAvail,
          showLoader: "",
          hide: "hide",
        });
        if(!this.state.movie.Error){
          this.setState({
            rating: this.state.movie.Ratings[0].Value
          });
        }
    })
    .catch(error => {
      console.log(error)
    })
    e.preventDefault();
  }

  updateInputVal(evt){
    this.setState({
      inputVal: evt.target.value
    })
    evt.preventDefault();
  }

  render(){
    return (
        <div className="App">

          {/* Header */}
          <img className={'header-img '+this.state.hide} src={require('../../assets/mickey.png')} alt="Header img"/>
          <h2 className="app-head">Sathish cinemas</h2>

          {/* Input */}
          <form>
            <input placeholder="Your search goes here" type="text" className="search-bar" ref={(input) => { this.nameInput = input; }} value={this.state.inputVal} onChange={evt => this.updateInputVal(evt)}/>
            <button className="app-button" onClick={e => this.getMovie(e)}><i className="fas fa-search"></i></button>
          </form>
        
          {/* Loader */}
          <div className={'loaderRoot ' + this.state.showLoader}>
          <div className="showbox">
            <div className="loader">
              <svg className="circular" viewBox="25 25 50 50">
                <circle className="path" cx="50" cy="50" r="20" fill="none" strokeWidth="2" strokeMiterlimit="10"/>
              </svg>
            </div>
          </div>
          </div>

          {/* Displays movie details */}
          {this.state.movie.Title ? 
            <div className="movie-details">
              <div className="title">{this.state.movie.Title}</div>
              
              <div className="movie-desc">

                <div className="img-container">
                <img src={this.state.movie.Poster} onError={(e)=>{e.target.onerror = null; e.target.src=require('../../assets/movie_placeholder.png')}} 
                alt="Movie poster"/>
                </div>
          
                <div className="desc">

                {this.state.movie.Plot ?
                  <div>
                    <p>Movie Overview:</p>
                    {this.state.movie.Plot}
                  </div>
                : null}

                  <div className="column-data">
                    <p>Language:</p>
                    <span>{this.state.movie.Language}</span>
                    <p>Runtime:</p>
                    <span>{this.state.movie.Runtime}</span>
                  </div>

                  {this.state.movie.Genre ?
                  <div>
                  <p>Genre:</p>
                  {this.state.movie.Genre}
                  </div>
                  : null}

                  {this.state.movie.Actors ? 
                  <div>
                  <p>Actors:</p>
                  {this.state.movie.Actors}
                  </div>
                  : null}

                  {this.state.movie.Writer ?
                  <div>
                  <p>Director:</p>
                  {this.state.movie.Writer}
                  </div>
                  : null}

                  {this.state.rating ? 
                  <div>
                  <p>Rating:</p>
                  {this.state.rating}
                  </div>
                  : null}

                  {this.state.movie.Released ?
                  <div>
                  <p>Release Date:</p>
                  {this.state.movie.Released}
                  </div>
                  : null}

                </div>

              </div>

            </div>
            : null
          }
          {/* Displays if no movie available */}
          {this.state.notfound ? <img className="no-movie" src={require('../../assets/no-result.png')} alt="No Movie Found"/>: null}

        </div>
    );
  }
}

export default Home;
