import React from 'react';
import './New.css';
import {Link, Redirect} from 'react-router-dom';

class New extends React.Component {

  constructor(props){
    super(props);

    if(localStorage.getItem("visited")>0){
      localStorage.setItem("visited", JSON.parse(localStorage.getItem("visited"))+1) 
    }else{
      localStorage.setItem("visited", 1)
    }

    this.state = {
      showRedirection: false,
      visited: localStorage.getItem("visited")
    }
  }

  componentDidMount(){
  }

  showRedirection(){
    this.setState({
      showRedirection: true
    });
  }

  render(){
    return (
      
      <div className="New">

        <div className="welcome-head">
          <h1 className="app-head">Sathish cinemas welcomes you</h1>
          <p>Search your favourite movies and get detailed information</p>
          {!this.state.showRedirection ?<button onClick={e => this.showRedirection(e)} className="app-button">Call Mickey to go</button>: null}
        </div>

        { this.state.showRedirection ?
          <div className="redirection">
          <button className="app-button"><Link to="/home">Click here</Link></button>
          <img src={require('../../assets/welcome.png')} alt="Welcome"/>
          </div>
        : null}
        
        { this.state.visited > 1 ? <Redirect to="/home"/>: null}

      </div>
    );
  }
}

export default New;
